@import url(https://fonts.googleapis.com/css?family=Mukta);
@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
body {
  font-family: "Mukta", sans-serif;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
}
a {
  text-decoration: none;
  color: #444444;
}
.login-reg-panel {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
  width: 70%;
  right: 0;
  left: 0;
  margin: auto;
  height: 400px;
  
}
.alert-danger{
  width: 150px;
  height: 35px;
  position: relative;
  top: 30px;
  text-align: center;
  margin: auto;
  background: rgba(189, 29, 29, 0.699);
  border: none;
  color: white;
  justify-content: center;
  
}
.white-panel {
  background-color: rgba(0, 0, 0, 0.144);
  height: 500px;
  position: absolute;
  top: -50px;
  width: 50%;
  right: calc(50% - 50px);
  transition: 0.3s ease-in-out;
  z-index: 0;
  box-shadow: 0 0 15px 9px #00000042;
  animation: opacity 5s ease;
  
}
.login-reg-panel input[type="radio"] {
  position: relative;
  display: none;
}
.login-reg-panel {
  color: #000000;
}
.login-reg-panel #label-login,
.login-reg-panel #label-register {
  border: 1px solid #9e9e9e;
  padding: 5px 5px;
  width: 150px;
  display: block;
  text-align: center;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  font-size: 18px;
}
.login-info-box {
  width: 30%;
  padding: 0 50px;
  top: 20%;
  left: 0;
  position: absolute;
  text-align: left;
  animation: fade 1ms ;
}
.register-info-box {
  width: 30%;
  padding: 0 50px;
  top: 20%;
  right: 0;
  position: absolute;
  text-align: left;
  animation: fade 1ms ease ;
  

}
.right-log {
  right: 0 !important;
}
.login-input,.register-input:hover{
  background-color: green;
}

.login-show,
.register-show {
  z-index: 1;
  display: none;
  opacity: 0;
  transition: 0.3s ease-in-out;
  color: #242424;
  text-align: center;
  padding: 50px;
}
.show-log-panel {
  display: block;
  opacity: 0.9;
}
.login-show input[type="text"],
.login-show input[type="password"] {
  width: 60%;
  display: block;
  margin: 20px auto;
  padding-top: auto;
  padding: auto;
  height: 35px;
  border: 1px solid #b5b5b5;
  outline: none;
  border-radius: 15px;
  text-align: center;
  position: relative;
  
}
.login-show input[type="button"] {
  max-width: 200px;
  width: 150px;
  background: #ffffff;
  margin: auto;
  color: #000000;
  border: none;
  height: 35px;
  text-transform: uppercase;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 15px;
  text-align: center;
  position: relative;
  top: 50px;
}

.login-show a {
  display: block;
  padding: 10px 0;
  margin: auto;
  position: relative;
  margin-top: 15px;
  position: relative;
  top: 50px;
}


.register-show input[type="text"],
.register-show input[type="password"] {
  width: 60%;
  display: block;
  margin-top: 15px;
  margin: auto;
  padding: 10px;
  border: 1px solid #b5b5b5;
  outline: none;
  border-radius: 15px;
  text-align: center;
  height: 35px;
  position: relative;
  top: 20px;
}
.register-show input[type="button"] {
  max-width: 200px;
  width: 150px;
  position: relative;
  top: 60px;
  background: #ffffff;
  color: #000000;
  border: none;
  padding: 10px;
  text-transform: uppercase;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 35px;
  text-align: center;
}
.credit {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: #3b3b25;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  z-index: 99;
}
a {
  text-decoration: none;
  color: #2c7715;
}
.form-container {
  position: relative;
  margin-top: 450px;
}
.Form-Container {
  display: none;
}
.change-button {
  display: none;
}
.bigPagebutton:hover{
animation-name:change ;
}
.white-panel p{
  font-size: 10px;
  width: 200px;
  height: auto;
}
.Login-Container{
  display: none;
}
@keyframes change{
    0%{background-color: rgba(0, 128, 0, 0.25);}
    25%{background-color: rgba(0, 128, 0, 0.50);}
    50%{background-color: rgba(0, 128, 0, 0.75);}
    100%{background-color: rgba(0, 128, 0, 0.95);}
}
@keyframes opacity{
  0%{background-color: rgb(0, 0, 0);}
  25%{background-color: rgba(0, 0, 0, 0.795);}
  50%{background-color: rgba(0, 0, 0, 0.555);}
  75%{background-color: rgba(0, 0, 0, 0.336);}
  100%{background-color: rgba(0, 0, 0, 0.144);}
}
@keyframes fade{
  from{
    opacity: 0;
  }

  to{
     opacity: 1;
  }
}

@media only screen and (max-width: 768px) {
    @keyframes gradient {
        0% {
            background-position: 0% 50%;
        }
        50% {
            background-position: 100% 50%;
        }
        100% {
            background-position: 0% 50%;
        }
    }
  body{
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 15s ease infinite;
  }
    .danger{
      background-color: rgba(223, 61, 61, 0.815);
      -webkit-text-decoration: white;
              text-decoration: white;
    }
  .login-reg-panel {
    display: none;
  }

  .Login-Container {
    display: block;
    margin: auto;
    width: 300px;
    height: 500px;
    background-color: rgba(255, 251, 251, 0.05);
    position: relative;
    
    border-radius: 15px;
    box-shadow: 6px 6px 2px 1px rgba(0, 0, 0, 0.5);
  }
  .Form-Container {
    display: block;
  }

  .Login-Container input[type="text"],
  .Login-Container input[type="password"] {
    width: 75%;
    display: block;
    margin: auto;
    padding-top: auto;
    padding: auto;
    height: 35px;
    border: 1px solid #b5b5b5;
    outline: none;
    border-radius: 15px;
    text-align: center;
    margin-top: 80px;
    position: relative;
    top: 90px;
  }
  .Login-Container input[type="button"] {
    width: 150px;
    background: #444444;
    color: #f9f9f9;
    border: none;
    padding: 10px;
    text-transform: uppercase;
    border-radius: 10px;
    cursor: pointer;
    top: 200px;
    position: relative;
    margin: auto;
    text-align: center;
    margin-left: 33%;
    justify-content: center;
  }
  .change-button {
    display: block;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    text-align: center;
    top: 50px;
    height: auto;
    
    margin: auto;
    width: 100px;
    height: 35px;
    color: black;
    background-color: rgba(240, 240, 240, 0.295);
    border: none;
    border-radius: 15px;
    animation-name:left-right;
    animation-duration: 2s;
    animation-iteration-count: infinite;
  }
  .change-button:hover{
      animation-duration: 3s;
      animation-name: left-right;
  }
  

  @keyframes left-right{
      0%{position:absolute;left: 0;}
      25%{ position:absolute;left: -50px;}
      50%{ position:absolute;left: 0;}
      75%{ position:absolute;left: 50px}
      100%{ position:absolute;left: 0}
      from{color: black;}
      to{color: white;}
    }
    button{
        font-family: 'Anton', sans-serif;
    }
    h2{
        font-size: 20px;
    }
}

